import { getUserProfile, updateUser } from '@/api/user';
import AcademySelector from '@/components/Selector/Academy';
import ClassroomSelector from '@/components/Selector/Classroom';
export default {
  components: {
    AcademySelector: AcademySelector,
    ClassroomSelector: ClassroomSelector
  },
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    uid: {
      type: undefined,
      required: true
    },
    edit: {
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      genderOptions: [{
        value: 1,
        label: '男'
      }, {
        value: 0,
        label: '女'
      }],
      defaultGenderValue: null,
      loading: true,
      form: this.$form.createForm(this),
      userProfile: {},
      updateLoading: false
    };
  },
  watch: {
    uid: function uid(_uid) {
      if (_uid !== undefined) {
        this.fetch(_uid);
      }
    }
  },
  methods: {
    fetch: function fetch(userId) {
      var _this = this;

      this.loading = true;
      getUserProfile(userId).then(function (res) {
        if (res.status) {
          _this.userProfile = res.data;
        }

        _this.loading = false;
      });
    },
    handleOk: function handleOk() {
      var _this2 = this;

      this.updateLoading = true;
      updateUser(this.uid, this.form.getFieldsValue()).then(function (res) {
        if (res.status) {
          _this2.$message.success('更新用户资料成功');

          _this2.$emit('success');
        } else {
          _this2.$message.error(res.message);
        }

        _this2.updateLoading = false;
      });
    },
