import ManualVerificationHandler from '@/components/ManualVerificationHandler';
import { getVerificationRecord as _getVerificationRecord } from '@/api/user';
var columns = [{
  dataIndex: 'id',
  title: '用户ID'
}, {
  dataIndex: 'created_at',
  title: '认证时间'
}, {
  dataIndex: 'remark',
  title: '认证过程'
}];
export default {
  components: {
    ManualVerificationHandler: ManualVerificationHandler
  },
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      list: [],
      columns: columns,
      user: {},
      handlerVisible: false,
      tableLoading: false
    };
  },
  watch: {
    user: {
      handler: function handler(val) {
        this.init(val.user_id);
      },
      deep: true
    }
  },
  created: function created() {
    this.init(this.user.user_id);
  },
  methods: {
    init: function init(userId) {
      this.getVerificationRecord(userId);
    },
    getVerificationRecord: function getVerificationRecord(userId) {
      var _this = this;

      this.tableLoading = true;

      _getVerificationRecord(userId).then(function (res) {
        _this.list = res.data;
        _this.tableLoading = false;
      });
    },
    handleManualVerificationClick: function handleManualVerificationClick() {
      this.handlerVisible = true;
    },
    setUser: function setUser(user) {
      this.user = user;
    },
    handleManualVerifySuccess: function handleManualVerifySuccess() {
      this.handlerVisible = false;
      this.init(this.user.user_id);
    }
  }
};