import AcademySelector from '@/components/Selector/Academy';
import CommonCardFrame from '@/components/CommonCardFrame';
import ClassroomSelector from '@/components/Selector/Classroom';
import RoleSelector from '@/components/Selector/Role';
import * as UtilsService from '@/utils/utils';
export default {
  components: {
    CommonCardFrame: CommonCardFrame,
    AcademySelector: AcademySelector,
    ClassroomSelector: ClassroomSelector,
    RoleSelector: RoleSelector
  },
  data: function data() {
    return {
      form: this.$form.createForm(this, {
        name: 'filterForm'
      })
    };
  },
  watch: {},
  methods: {
    handleSubmit: function handleSubmit(e) {
      var _this = this;

      e.preventDefault();
      this.form.validateFields(function (err, values) {
        if (!err) {
          // 去除空对象
          UtilsService.deleteEmptyProperty(values);

          _this.transformSelectorData(values);

          _this.$emit('filterSubmit', values);
        }
      });
    },
    search: function search() {
      this.$emit('search');
    },
    reset: function reset() {
      this.form.resetFields();
    },
    refresh: function refresh() {
      this.$emit('refresh');
    },
    transformSelectorData: function transformSelectorData(values) {
      if (values.academy !== undefined) {
        var academyId = values.academy.key;
        delete values.academy;
        values.academy_id = academyId;
      }

      if (values.classroom !== undefined) {
        var classroomId = values.classroom.key;
        delete values.classroom;
        values.classroom_id = classroomId;
      }

      if (values.roles !== undefined) {
        var roleId = values.roles.key;
        delete values.roles;
        values.role_id = roleId;
      }
    }
  }
};