import * as UserService from '@/api/user';
var columns = [{
  title: '姓名',
  dataIndex: 'real_name',
  width: 80
}, {
  title: '昵称',
  dataIndex: 'nick_name',
  scopedSlots: {
    customRender: 'nick_name'
  },
  width: 100
}, {
  title: '用户头像',
  dataIndex: 'avatar',
  scopedSlots: {
    customRender: 'avatar'
  },
  width: 50
}, {
  title: '手机号',
  dataIndex: 'phone'
}, {
  title: '性别',
  dataIndex: 'gender',
  scopedSlots: {
    customRender: 'gender'
  }
}, {
  title: '所在学院',
  dataIndex: 'academy.academy_name'
}, {
  title: '所属班级',
  dataIndex: 'classroom.class_name'
}, {
  title: '总积分',
  dataIndex: 'score.total_score'
}, {
  title: '实名认证',
  dataIndex: 'is_verified',
  scopedSlots: {
    customRender: 'isVerified'
  }
}, {
  title: '学号',
  dataIndex: 'stu_num'
}, {
  title: '权限角色',
  dataIndex: 'roles',
  scopedSlots: {
    customRender: 'roles'
  }
}, {
  title: '最后登录IP',
  dataIndex: 'last_ip'
}, {
  title: '最后登录时间',
  dataIndex: 'last_login'
}, {
  title: '用户状态',
  dataIndex: 'status',
  scopedSlots: {
    customRender: 'status'
  }
}, {
  title: '操作',
  scopedSlots: {
    customRender: 'operation'
  }
}];
export default {
  data: function data() {
    return {
      loading: true,
      userList: [],
      columns: columns
    };
  },
  created: function created() {
    this.init();
  },
  methods: {
    init: function init(params) {
      var _this = this;

      this.loading = true;
      UserService.getUserList(params).then(function (res) {
        if (res.status) {
          _this.userList = res.data.data;

          _this.$emit('paginationData', res.data);

          _this.loading = false;
        }
      });
    },

    /**
     * 账户的启用与停用
     * @id 账号的 id pk
     * @shouldEnable 是否启用 Boolean
     */
    handleStatusEdit: function handleStatusEdit(id, status) {
      // 取反状态
      var params = {
        status: !status ? '1' : '0'
      };
      this.updateUser(id, params);
    },

    /**
     * 把表格选中的那一列的数据传给父组件
     */
    handleMenuClick: function handleMenuClick(e, row) {
      var userData = {
        id: row.id,
        real_name: row.real_name,
        roles: row.roles
      };

      switch (e.key) {
        case 'editUser':
          this.$emit('editUser', row.id);
          break;

        case 'showUser':
          this.$emit('showUser', row.id);
          break;

        case 'editRole':
          this.$emit('editRole', userData);
          break;

        case 'editScore':
          this.$emit('editScore', {
            total_score: row.score.total_score,
            user_id: row.id
          });
          break;

        case 'verificationRecord':
          this.$emit('verificationRecord', {
            user_id: row.id
          });
      }
    },

    /**
     * 更新用户
     * @params 参数
     */
    updateUser: function updateUser(id, params) {
      var _this2 = this;

      this.loading = true;
      UserService.updateUser(id, params).then(function (res) {
        if (res.status) {
          _this2.$message.success('操作成功');

          _this2.init();
        }
      });
    }
  }
};